import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex) // vue

// Vuex.Store 
const store = new Vuex.Store({
    // datastate$
    state: {
        // 
        $userInfo: {}
    },
	getters:{
		getUserInfo(state){
			return state.$userInfo
		}
	},
	mutations:{
		setUserInfo(state,payload){
			console.log('setUserInfo: ',payload.nickName)
			state.$userInfo = payload
		}, 
	}
})

export default store
